import { getDoc } from "./ApiRequests";

var docInfo = {
  state: 'loading',
  docid: '',
  data: {name: ''},
  onChange: () => { },
}

docInfo.reset = () => {
  docInfo.state = 'loading';
  docInfo.data = {name: ''}
}

docInfo.get = async () => {
  docInfo.state = 'loading'
  try {
    let resp = await getDoc(docInfo.docid);
    docInfo.data = resp.data;
    docInfo.state = 'available';
  } catch(err) {
    console.log(err);
    docInfo.state = 'error';
  }
  if (docInfo.onChange) {
    try {
      docInfo.onChange();
    } catch(err) {
      console.log(err)
    }
  }
}

export default docInfo;
