import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import "./App.css";
import AppComponent from "./App"
import { HashRouter } from "react-router-dom";
import reportWebVitals from "./reportWebVitals";
import Routes from "./routes/Routes";
import qs from 'qs';
import docInfo from './utils/DocInfo'
import {hello} from './utils/ApiRequests'

var openDoc = false;
var params = qs.parse(window.location.search, { ignoreQueryPrefix: true })

var docid = params.docid;
var _new = params._new;
var offline = params.offline;
var ref = params.ref;

if (ref) {
  hello(ref);
  if (!docid && !_new && window.location.pathname == '/' && window.location.hash == '') {
    window.history.replaceState({}, "", "/")
  }
}

if (window.location.pathname == '/' && (!!docid || _new == 'true' || offline == 'true')) {
  docInfo.docid = docid;
  openDoc = true;
}

ReactDOM.render(
  <React.StrictMode>
    {openDoc&& <AppComponent />}
    {!openDoc &&<HashRouter>
      <Routes />
    </HashRouter>}
  </React.StrictMode>,
  document.getElementById("root")
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
